
import "./style/widgets.css";
import {changeHeight, showPosition} from './util/util3D'
import Viewer from './core/Viewer';


let viewer:Viewer;

export default {
  name: 'App',
  components: {
    
  },
  data:()=>{
    return {
      offx:7,
      offy:-11.2,
      height:-24.4,
      isInit:false,
    }
  },
  methods:{
    onOffChange(){
        changeHeight(viewer.build,this.offx,this.offy,this.height)
    }
  },
  mounted(){
         viewer = new Viewer()
    
      // 设置沙箱允许使用JS
      var iframe = document.getElementsByClassName("cesium-infoBox-iframe")[0];
      iframe.setAttribute(
        "sandbox",
        "allow-same-origin allow-scripts allow-popups allow-forms"
      );
      iframe.setAttribute("src", "");

    viewer.createBuild('OSGB/tileset.json').then(() => {
      this.isInit=true;
      this.onOffChange()         
    });
    

    showPosition(viewer.viewer)

    // viewer.camera.flyTo({
    //   destination: Cesium.Cartesian3.fromDegrees(116.39, 39.9, 1000),
    //   orientation: {
    //     heading: Cesium.Math.toRadians(0),
    //     pitch: Cesium.Math.toRadians(-90),
    //     roll: 0.0,
    //   },
    // });
  }
}
